body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1280px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

.clearfix::after {
  content: "";
  display: block;
  clear: both;
}

@font-face {
  font-family: "Open Sans - Semibold";
  src: url(../fonts/open_sans_semibold.ttf);
}

@font-face {
  font-family: "Raleway";
  src: url(../fonts/raleway.ttf);
}

@font-face {
  font-family: "Raleway Regular";
  src: url(../fonts/raleway_regular.ttf);
}

@font-face {
  font-family: "Raleway - Semi Bold";
  src: url(../fonts/raleway_semibold.ttf);
}

@font-face {
  font-family: "Open Sans";
  src: url(../fonts/open_sans.ttf);
}

@font-face {
  font-family: "Poppins";
  src: url(../fonts/Poppinsregular.ttf);
}

@font-face {
  font-family: "Roboto";
  src: url(../fonts/Robotomedium.ttf);
}

.firstsc {
  min-height: 881px;
  background-color: #6500ff;
  display: flex;
  flex-direction: column;
}

.firstsc .firstsc_nav .wrapper {
  padding-top: 60px;
  display: flex;
  justify-content: space-between;
}

.firstsc .firstsc_nav .wrapper #logo {
  color: #ffffff;
  font-family: Roboto;
  font-size: 37px;
  font-weight: 700;
  font-style: italic;
  line-height: 60.19px;
  text-transform: uppercase;
}

.firstsc .firstsc_nav .wrapper .firstsc_nav_menu {
  position: relative;
}

.firstsc .firstsc_nav .wrapper .firstsc_nav_menu .menu_toggle {
  display: none;
}

.firstsc .firstsc_nav .wrapper .firstsc_nav_menu .menu li {
  float: left;
}

.firstsc .firstsc_nav .wrapper .firstsc_nav_menu .menu li a {
  color: #ffffff;
  font-family: Raleway Regular;
  font-size: 18px;
  text-decoration: none;
  margin: 0 16px;
  line-height: 65px;
}

.firstsc .firstsc_nav .wrapper .sign_in {
  width: 120px;
  height: 45px;
  background-color: #ff5f60;
  border-radius: 30px;
  text-align: center;
}

.firstsc .firstsc_content {
  margin: auto 0;
}

.firstsc .firstsc_content .wrapper .wrapper_in {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.firstsc .firstsc_content .wrapper .wrapper_in .firstsc_text {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(50% - 32px);
  display: flex;
  flex-direction: column;
}

.firstsc .firstsc_content .wrapper .wrapper_in .firstsc_text .firstsc_text_in {
  min-height: 30px;
  margin: auto 0;
}

.firstsc .firstsc_content .wrapper .wrapper_in .firstsc_text .firstsc_text_in .first_text_in_wrap {
  margin: 0 auto;
}

.firstsc .firstsc_content .wrapper .wrapper_in .firstsc_text .firstsc_text_in .first_text_in_wrap h2 {
  color: #ffffff;
  font-family: Raleway;
  font-size: 60px;
  font-weight: 700;
  line-height: 75px;
  margin-top: 35px;
  margin-bottom: 35px;
}

.firstsc .firstsc_content .wrapper .wrapper_in .firstsc_text .firstsc_text_in .first_text_in_wrap .wrap_p {
  display: flex;
  flex-direction: column;
}

.firstsc .firstsc_content .wrapper .wrapper_in .firstsc_text .firstsc_text_in .first_text_in_wrap .wrap_p p {
  color: #ffffff;
  font-family: "Open Sans - Semibold";
  font-size: 17px;
  font-weight: 400;
  line-height: 30px;
  display: inline-block;
  width: 80%;
}

.firstsc .firstsc_content .wrapper .wrapper_in .firstsc_text .firstsc_text_in .first_text_in_wrap .button_more button {
  width: 150px;
  height: 50px;
  background-color: #ff5f60;
  border: none;
  cursor: pointer;
  color: #ffffff;
  font-family: Raleway Regular;
  font-size: 18px;
  font-weight: 500;
  border-radius: 25px;
  margin: 50px 0 50px 0;
}

.firstsc .firstsc_content .wrapper .wrapper_in .firstsc_img {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(50% - 32px);
  min-height: 486px;
  margin-top: 30px;
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
}

.firstsc .firstsc_content .wrapper .wrapper_in .firstsc_img .center_img {
  margin: auto 0;
}

.secondsc {
  padding-top: 70px;
  position: relative;
}

.secondsc .secondsc_img {
  position: absolute;
  z-index: -1;
  min-height: 500px;
  background-image: url(../img/Shape_1.png);
  background-repeat: no-repeat;
  width: 100%;
}

.secondsc .wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.secondsc .wrapper .secondsc_left {
  padding-top: 50px;
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(50% - 32px);
}

.secondsc .wrapper .secondsc_left .center_img {
  display: flex;
  flex-direction: column;
}

.secondsc .wrapper .secondsc_left .center_img img {
  width: 70%;
  margin: 0 auto;
}

.secondsc .wrapper .secondsc_right {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(50% - 32px);
}

.secondsc .wrapper .secondsc_right h2 {
  color: #333333;
  font-family: "Raleway - Semi Bold";
  font-size: 36px;
  font-weight: 400;
  margin: 30px 0;
}

.secondsc .wrapper .secondsc_right .secondsc_right_text {
  color: #8c8c8c;
  font-family: "Open Sans";
  font-size: 17px;
  font-weight: 400;
  line-height: 27px;
  margin: 30px 0;
}

.secondsc .wrapper .secondsc_right .secondsc_right_button button {
  width: 170px;
  height: 45px;
  background-color: #ff5f60;
  border: none;
  border-radius: 25px;
  margin: 30px 0;
  color: #ffffff;
  font-family: "Raleway - Semi Bold";
  font-size: 16px;
  font-weight: 400;
  cursor: pointer;
}

.thirdsc {
  background: #F7F6FE;
  margin-top: 70px;
  margin-bottom: 70px;
}

.thirdsc .wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.thirdsc .wrapper .secondsc_left {
  order: 2;
  padding-top: 50px;
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(50% - 32px);
}

.thirdsc .wrapper .secondsc_left .center_img {
  display: flex;
  flex-direction: column;
}

.thirdsc .wrapper .secondsc_left .center_img img {
  width: 70%;
  margin: 0 auto;
}

.thirdsc .wrapper .secondsc_right {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(50% - 32px);
}

.thirdsc .wrapper .secondsc_right h2 {
  color: #333333;
  font-family: "Raleway - Semi Bold";
  font-size: 36px;
  font-weight: 400;
  margin: 30px 0;
}

.thirdsc .wrapper .secondsc_right .secondsc_right_text {
  color: #8c8c8c;
  font-family: "Open Sans";
  font-size: 17px;
  font-weight: 400;
  line-height: 27px;
  margin: 30px 0;
}

.thirdsc .wrapper .secondsc_right .secondsc_right_button button {
  width: 170px;
  height: 45px;
  background-color: #ff5f60;
  border: none;
  border-radius: 25px;
  margin: 30px 0;
  color: #ffffff;
  font-family: "Raleway - Semi Bold";
  font-size: 16px;
  font-weight: 400;
  cursor: pointer;
}

.fourthsc .wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.fourthsc .wrapper .fourthsc_item {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 32px);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 30px;
}

.fourthsc .wrapper .fourthsc_item h2 {
  color: #333333;
  font-family: "Raleway - Semi Bold";
  font-size: 25px;
  font-weight: 400;
}

.fourthsc .wrapper .fourthsc_item div {
  width: 80%;
  color: #8c8c8c;
  font-family: Poppins;
  font-size: 17px;
  font-weight: 400;
  line-height: 27px;
  text-align: center;
}

footer .wrapper {
  padding: 40px 0;
  border-top: 1px solid #f3f0ff;
  display: flex;
  justify-content: space-between;
}

footer .wrapper .footer_left {
  padding-left: 16px;
  color: #c9c9c9;
  font-family: Poppins;
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
}

footer .wrapper .footer_right_menu ul li {
  float: left;
  display: inline-block;
  margin: 0 16px;
}

footer .wrapper .footer_right_menu ul li a {
  color: #c9c9c9;
  font-family: Poppins;
  font-size: 14px;
  font-weight: 400;
  line-height: 25px;
  text-decoration: none;
  letter-spacing: 0.28px;
}

@media screen and (max-width: 1200px) {
  .wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .firstsc .firstsc_nav .wrapper .firstsc_nav_menu .menu_toggle {
    display: block;
  }

  .firstsc .firstsc_nav .wrapper .firstsc_nav_menu .menu {
    display: none;
  }

  .firstsc .firstsc_content .wrapper .wrapper_in .firstsc_text {
    width: calc(83.33333333333334% - 32px);
  }

  .firstsc .firstsc_content .wrapper .wrapper_in .firstsc_img {
    width: calc(83.33333333333334% - 32px);
  }

  .secondsc .wrapper .secondsc_left {
    width: calc(83.33333333333334% - 32px);
  }

  .secondsc .wrapper .secondsc_right {
    width: calc(83.33333333333334% - 32px);
  }

  .thirdsc .wrapper .secondsc_left {
    order: -1;
  }

  .thirdsc .wrapper .secondsc_left {
    width: calc(83.33333333333334% - 32px);
  }

  .thirdsc .wrapper .secondsc_right {
    width: calc(83.33333333333334% - 32px);
  }

  .fourthsc .wrapper .fourthsc_item {
    width: calc(50% - 32px);
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .firstsc {
    min-height: 80px;
  }

  .firstsc .firstsc_content .wrapper .wrapper_in .firstsc_text .firstsc_text_in .first_text_in_wrap h2 {
    font-size: 40px;
  }

  .firstsc .firstsc_content .wrapper .wrapper_in .firstsc_text .firstsc_text_in .first_text_in_wrap h2 {
    line-height: 40px;
  }

  .firstsc .firstsc_content .wrapper .wrapper_in .firstsc_text .firstsc_text_in .first_text_in_wrap h2 {
    margin-bottom: 16px;
  }

  .firstsc .firstsc_content .wrapper .wrapper_in .firstsc_text .firstsc_text_in .first_text_in_wrap h2 {
    margin-top: 16px;
  }

  .firstsc .firstsc_content .wrapper .wrapper_in .firstsc_text .firstsc_text_in .first_text_in_wrap .wrap_p p {
    font-size: 15px;
  }

  .firstsc .firstsc_content .wrapper .wrapper_in .firstsc_text .firstsc_text_in .first_text_in_wrap .wrap_p p {
    width: 100%;
  }

  .firstsc .firstsc_content .wrapper .wrapper_in .firstsc_text .firstsc_text_in .first_text_in_wrap .button_more button {
    margin-top: 25px;
  }

  .firstsc .firstsc_content .wrapper .wrapper_in .firstsc_text .firstsc_text_in .first_text_in_wrap .button_more button {
    margin-bottom: 25px;
  }

  .firstsc .firstsc_content .wrapper .wrapper_in .firstsc_img {
    min-height: 143px;
  }

  .secondsc .wrapper .secondsc_right h2 {
    font-size: 26px;
  }

  .secondsc .wrapper .secondsc_right .secondsc_right_text {
    font-size: 15px;
  }

  .thirdsc .wrapper .secondsc_right h2 {
    font-size: 26px;
  }

  .thirdsc .wrapper .secondsc_right .secondsc_right_text {
    font-size: 15px;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .firstsc .firstsc_content .wrapper .wrapper_in .firstsc_text {
    width: calc(100% - 32px);
  }

  .firstsc .firstsc_content .wrapper .wrapper_in .firstsc_img {
    width: calc(100% - 32px);
  }

  .secondsc .wrapper .secondsc_left {
    width: calc(100% - 32px);
  }

  .secondsc .wrapper .secondsc_right {
    width: calc(100% - 32px);
  }

  .thirdsc .wrapper .secondsc_left {
    width: calc(100% - 32px);
  }

  .thirdsc .wrapper .secondsc_right {
    width: calc(100% - 32px);
  }

  .fourthsc .wrapper .fourthsc_item {
    width: calc(100% - 32px);
  }

  .fourthsc .wrapper .fourthsc_item div {
    width: 100%;
  }
}