.firstsc {
  // width: 100%;
  //border: 1px solid red;
  min-height: 881px;
  .sm(min-height, 80px);
  // max-width: 100%;
  background-color: #6500ff;
  display: flex;
  flex-direction: column;
  .firstsc_nav {
    //border: 1px solid black;
    .wrapper {
      #logo {
        img {
          // border: 1px solid red;
          // margin-right: -2px;
        }
        color: #ffffff;
        font-family: Roboto;
        font-size: 37px;
        font-weight: 700;
        font-style: italic;
        line-height: 60.19px;
        text-transform: uppercase;
      }
      // background-color: red;
      // border: 1px solid black;
      padding-top: 60px;
      display: flex;
      justify-content: space-between;
      .firstsc_nav_menu {
        position: relative;
        .menu_toggle {
          display: none;
          .md(display, block);
        }
        .menu {
          .md(display, none);
          li {
            float: left;
            a {
              color: #ffffff;
              font-family: Raleway Regular;
              font-size: 18px;
              text-decoration: none;
              margin: 0 16px;
              // font-weight: 500;
              line-height: 65px;
            }
          } 
        }
      }
      .sign_in {
        width: 120px;
        height: 45px;
        background-color: #ff5f60;
        border-radius: 30px;
        text-align: center;

      }
    }
  }
  .firstsc_content {
    margin: auto 0;
    #wrap_content {
      //border: 2px solid green;
      // flex-basis: 2;
      //height: 100%;
      //margin: auto 0;
      // margin-top: auto;
      // display: flex;
      // flex-direction: column;
    }
    .wrapper {
      //border: 2px solid green;
      //margin: 0 auto;
      // margin: 0;
      // background-color: cyan;
      //border: 1px solid red;
      //.row-flex();
      .wrapper_in {
        .row-flex();
        justify-content: center;
        .firstsc_text {
          //border: 2px solid red;
          .col();
          .size(6);
          .size-md(10);
          .size-xs(12);
          // min-height: 486px;
          //background: #ddd;
          //border: 1px solid blue;
          display: flex;
          flex-direction: column;
          .firstsc_text_in {
            min-height: 30px;
            //background: coral;
            margin: auto 0;
            .first_text_in_wrap {
              //border: 1px solid green;
              // width: 90%;
              // .sm(width, 100%);
              margin: 0 auto;
              // text-align: left;
              h2 {
                .sm(font-size, 40px);
                .sm(line-height, 40px);
                .sm(margin-bottom, 16px);
                .sm(margin-top, 16px);
                color: #ffffff;
                font-family: Raleway;
                font-size: 60px;
                font-weight: 700;
                line-height: 75px;
                margin-top: 35px;
                margin-bottom: 35px;
                // text-align: center;
              }
              .wrap_p {
                display: flex;
                flex-direction: column;
                //border: 1px solid red;
                p {
                  color: #ffffff;
                  font-family: "Open Sans - Semibold";
                  font-size: 17px;
                  .sm(font-size, 15px);
                  font-weight: 400;
                  line-height: 30px;
                  display: inline-block;
                  //border: 1px solid green;
                  width: 80%;
                  .sm(width, 100%);
                }
              }
              .button_more button {
                width: 150px;
                height: 50px;
                background-color: #ff5f60;
                border: none;
                cursor: pointer;
                color: #ffffff;
                font-family: Raleway Regular;
                font-size: 18px;
                font-weight: 500;
                //line-height: 65px;
                border-radius: 25px;
                margin: 50px 0 50px 0;
                .sm(margin-top, 25px);
                .sm(margin-bottom, 25px);
              }
            }
            
          }
        }
        .firstsc_img {

          .col();
          .size(6);
          .size-md(10);
          .size-xs(12);
          min-height: 486px;
          .sm(min-height, 143px);
          //border: 1px solid black;
          margin-top: 30px;
          margin-bottom: 30px;
          display: flex;
          flex-direction: column;
          .center_img {
            //border: 1px solid red;
            margin: auto 0;
          }
          // vertical-align: center;
          // background-image: url(../img/students.png);
          // background-repeat: no-repeat;
          // background-size: contain;
          //background: yellow;
          // .center {
          //   display: block;
          //   margin: auto 0;
          //   border: 1px solid red;
          // }
        }
      }
      
    }
  }
  
}

.secondsc {
  padding-top: 70px;
  // margin-top: 70px;
  // background-image: linear-gradient(to left, white 50%, #F7F6FE 50%);
  // .md(background-image, linear-gradient(to right, white 50%, white 50%));
  // border: 1px solid red;
  // background-image: url(../img/Shape_11.png);
  // background-size: 1700px;
  // background-position: -380px -190px;
  // background-repeat: no-repeat;
  // padding-bottom: 50px;
  position: relative;
  .secondsc_img {
    position: absolute;
    z-index: -1;
    min-height: 500px; //временное значение
    background-image: url(../img/Shape_1.png);
    //background-size: contain;
    background-repeat: no-repeat;
    width: 100%;
  }
  .wrapper {
    //border: 1px solid green;
    .row-flex();
    justify-content: center;
    .secondsc_left {
      //border: 1px solid yellow;
      padding-top: 50px;
      .col();
      .size(6);
      .size-md(10);
      .size-xs(12);
      .center_img {
        //border: 1px solid red;
        display: flex;
        flex-direction: column;
        img {
          width: 70%;
          margin: 0 auto;
        }
      }
      
    }
    .secondsc_right {
      //border: 1px solid yellow;
      .col();
      .size(6);
      .size-md(10);
      .size-xs(12);
      h2 {
        color: #333333;
        font-family: "Raleway - Semi Bold";
        font-size: 36px;
        font-weight: 400;
        margin: 30px 0;
        .sm(font-size, 26px);
      }
      .secondsc_right_text {
        color: #8c8c8c;
        font-family: "Open Sans";
        font-size: 17px;
        .sm(font-size, 15px);
        font-weight: 400;
        line-height: 27px;
        margin: 30px 0;
      }
      .secondsc_right_button button{
        width: 170px;
        height: 45px;
        background-color: #ff5f60;
        border: none;
        border-radius: 25px;
        margin: 30px 0;
        color: #ffffff;
        font-family: "Raleway - Semi Bold";
        font-size: 16px;
        font-weight: 400;
        cursor: pointer;
      }
    }
  }
}





