.fourthsc {
  .wrapper {
    .row-flex();
    justify-content: center;
    .fourthsc_item {
      //min-height: 300px;
      //border: 1px solid black;
      .col();
      .size(4);
      .size-md(6);
      .size-xs(12);
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-bottom: 30px;
      h2 {
        color: #333333;
        font-family: "Raleway - Semi Bold";
        font-size: 25px;
        font-weight: 400;
      }
      div {
        width: 80%;
        .xs(width, 100%);
        color: #8c8c8c;
        font-family: Poppins;
        font-size: 17px;
        font-weight: 400;
        line-height: 27px;
        text-align: center;
      }
    }
  }
}

